@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
.App {
    width: 30%;
    margin: 0px auto;
    font-family: 'Lato';

    padding: 2em 0px;
}

.sectionBlock h2 {
    display: block;
    border-bottom: 1px solid rgb(225, 225, 225);
    padding: .5em 0px;
}

.authPage {
    background: #fff;
    padding: 2em 3em;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    box-shadow: 0px 10px 10px rgba(200, 200, 200, .25)
}

.websiteTitle {
    margin: 0px;
    border-bottom: 1px solid rgb(225, 225, 225);
    padding-bottom: .7em;
    text-align: center;
    letter-spacing: .2em;
}

.pageTitle {
    text-align: center;
    letter-spacing: .2em;
}

.expensesInfo {
    display: flex;
    justify-content: space-between;
    align-items: center;

    color: #333;
}

p {
    color: #777;
    font-size: 16px!important;
}
.expensesBlock {
    display: flex;
    background: #fff;
    box-shadow: 0px 3px 5px rgba(100, 100, 100, .25);
    align-items: center;
}

.amountOfMoney {
    flex: 1 1;
    padding: 2em;
    text-align: center;  
    background: #fff; 
}

.dividerBlock {
    width: 1px;
    background: rgb(225, 225, 225);
    height: 75px;
    border-radius: 5px;
}

.expensesBlock span {
    text-transform: uppercase;
    font-weight: 18px;
}

.expensesBlock  p {
    font-weight: 600;
    font-size: 20px;
    margin: .5em 0px 0px 0px;
}

.earned {
    color: rgb(89, 230, 130)
}

.spent {
    color: rgb(230, 89, 89)
}
.expenseBlock {
    width: 100%;
    position: relative;
    cursor: pointer;
    margin: 1em 0px;
    transition: .4s ease;
}

.removeItem {
    position: absolute;
    height: 100%;
    min-width: 10%;

    left: 100%;
    top: 0px;

    background: rgb(230, 89, 89);
    padding: .5em;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: .4s ease;
    z-index: 9999;
}

.removeItem:hover {
    background: rgb(240, 89, 89);
}

.removeItem img {
    width: 100%;
}

.expenseItem__block {
    width: 100%;
    background: #fff;
    box-shadow: 0px 3px 3px rgba(200, 200, 200, .25);
    padding: 1em;

    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    transition: .4s ease;
}

.expenseItem__block::after {
    position: absolute;
    content: '';

    width: 6px;
    height: 100%;

    top: 0px;
    left: 100%;
    transform: translateX(-100%);
}

.incomeItem::after {
    background: rgb(49, 146, 49);
}

.expenseItem::after {
    background: rgb(146, 49, 49);
}

.toClose {
    transform: translateX(-10%);
}
.expensesList {
    max-height: 30vh;
    overflow: auto;
    overflow-x: hidden;
}

.expensesList h4 {
    color: #777;
    margin: 0px;
}
.transactionBlock {
    display: flex;
    flex-direction: column;
}

.transactionBlock label {
    font-size: 14px;
    font-weight: 700;
    letter-spacing: .1em;
}

.transactionBlock input {
    padding: .7em .5em;
    font-size: 14px;

    outline: none;
    margin: .6em 0px .8em 0px;
    color: #333;
    font-family: 'Lato';

    border: 1px solid rgb(170, 170, 170);
}

.transactionBlock button {
    padding: 1em;
    background: rgb(155, 79, 226);

    color: #fff;
    font-weight: 700;
    font-size: 14px;
    border: 0px;

    cursor: pointer;
}
* {
    box-sizing: border-box;
}

body {
    background: #fafafa;
    color: #333;
}
